import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			productList: [],
			totalPrice: '',
			total_num:'',
			show: false,
			radio: '1',
			name: '',
			avatar: '',
			userInfo:'',
			obj:''
		}
	},
	methods: {
		// 结算弹窗
		popup() {
			this.show = true;
		},
		onClose() {
			this.show = false;
		},
		onClick(e) {
			this.radio = e.currentTarget.dataset.name
		},
		onGotUserInfo: function() {
			var that = this
			// wx登录
			wx.login({
				success(res) {
					if (res.code) {
						//发起网络请求
						var code = res.code
						// 获取微信用户信息
						wx.getUserInfo({
							success: function(res) {
								that.name = res.userInfo.nickName
								that.avatar = res.userInfo.avatarUrl
							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})

					} else {
						
					}
				}
			})
		},
		location(){
			wx.navigateTo({
				url:"../location/location"
			})
		}
	},
	onLoad(options){
		this.obj=JSON.parse(options.obj)
		console.log(this.obj)
		this.operations=options.operations
	},
	onShow() {
		var that = this;
		if(that.operations==0){
			// 拼团购买
			console.log(this.obj)
			uni.request({
				url:baseUrl+'cart/settleAccounts',
				method:'POST',
				data:{
					rec_id_str:'',
					total_num:this.obj.total_num,
					total_price:this.obj.total_price,
					goods_id:this.obj.goods_id
				},
				success(e){
					console.log(e.data.data.cart_goods)
					that.productList=[e.data.data.cart_goods]
					that.totalPrice=e.data.data.total_price
					that.total_num=e.data.data.total_num
				}
			})
			
		}else if(that.operations==1){
			// 单独购买
			console.log(this.obj)
			uni.request({
				url:baseUrl+'cart/settleAccounts',
				method:'POST',
				data:{
					rec_id_str:this.obj.rec_id_str,
					total_num:this.obj.total_num,
					total_price:this.obj.total_price,
					goods_id:this.obj.goods_id
				},
				success(e){
					// that.cart_goods=JSON.stringify(e.data.data)
					console.log(e.data.data)
					that.productList=e.data.data.cart_goods
					that.totalPrice=e.data.data.total_price
					that.total_num=e.data.data.total_num
				}
			})	
		}
		uni.getStorage({
			key:"userInfo",
			success(e){
				that.userInfo=JSON.parse(e.data)
				// console.log(e.data)
			}
		})
	}
}
